import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { BehaviorSubject, merge, Observable, Subscription } from 'rxjs';
import { IStandings, LeaguesService, LoadingService } from '@aft-core';
import { Router } from '@angular/router';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-scout-standings',
  templateUrl: './league-standings.component.html',
  styleUrls: ['./league-standings.component.scss'],
})
export class LeagueStandingsComponent implements OnInit, OnDestroy {
  @Input()
  seasonId$: BehaviorSubject<number | undefined> | undefined;

  isLoading$: Observable<boolean>;
  leagueStandings$: BehaviorSubject<IStandings | undefined>;
  private _subscriptions: Array<Subscription> = [];

  constructor(private _leaguesService: LeaguesService, private _loadingService: LoadingService, private _router: Router) {
    this.leagueStandings$ = this._leaguesService.leagueStandings$;
    this.isLoading$ = merge(this._leaguesService.isLoading$, this._loadingService.loading$);
  }

  ngOnInit(): void {
    if (this.seasonId$) {
      this._subscriptions.push(
        this.seasonId$.subscribe(seasonId => {
          if (seasonId) {
            this._leaguesService.getLeagueStandings(seasonId);
          }
        })
      );
    } else {
      throw new Error('Season parameter not recieved by input');
    }
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(x => x.unsubscribe());
  }

  goToTeam(teamId: number): void {
    this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, teamId));
  }
}
