// eslint-disable-next-line @typescript-eslint/naming-convention
import { APP_ROUTES } from '@aft-core';

// eslint-disable-next-line @typescript-eslint/naming-convention
export const LEAGUE_ROUTES = {
  LEAGUE_ROUTE: '',
  LEAGUE_STANDINGS_ROUTE: 'standings',
};

export const generateLeagueModuleRoute = (route: string): Array<string> => {
  if (route?.length) {
    return [route];
  }
  return [];
};

export const generateFullPrivateModuleRoute = (route: string, params?: any): Array<string> => {
  const routeArray = [...APP_ROUTES.PRIVATE, ...generateLeagueModuleRoute(route)];
  if (params) {
    routeArray.push(params);
  }
  return routeArray;
};
