import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { LeagueDetailComponent } from './components/league-detail/league-detail.component';
import { LeagueStandingsComponent } from './components/league-standings/league-standings.component';
import { LEAGUE_ROUTES } from './league.routes';

const routes: Routes = [
  {
    path: LEAGUE_ROUTES.LEAGUE_ROUTE,
    component: LeagueDetailComponent,
  },
  {
    path: LEAGUE_ROUTES.LEAGUE_STANDINGS_ROUTE,
    component: LeagueStandingsComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LeagueRoutingModule {}
