import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { BehaviorSubject, Subscription } from 'rxjs';
import {
  APP_ROUTES,
  ENTITIES_ENUM,
  EventsService,
  ILeague,
  IEvent,
  ITeam,
  LeaguesService,
  MessagingService,
  TeamsPlayersService,
} from '@aft-core';

@Component({
  selector: 'aft-scout-league',
  templateUrl: './league-detail.component.html',
  styleUrls: ['./league-detail.component.scss'],
})
export class LeagueDetailComponent implements OnInit, OnDestroy {
  leagueIdParam: string | undefined;
  league$: BehaviorSubject<ILeague | undefined>;
  currentLeague: ILeague | undefined;
  teams$: BehaviorSubject<ITeam[]>;
  leagueEvents$: BehaviorSubject<Array<IEvent>>;
  leagueTeams: ITeam[] = [];
  hiddenTeamsColumns: string[];
  activeSeasonId$: BehaviorSubject<number | undefined>;

  entitiesEnum = ENTITIES_ENUM;
  private _leagues: Array<ILeague>;
  private _subscriptions$: Subscription[] = [];

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _leaguesService: LeaguesService,
    private _teamsPlayersService: TeamsPlayersService,
    private _eventsService: EventsService,
    private _messagingService: MessagingService
  ) {
    this._leagues = this._leaguesService.leagues$.value;
    this.teams$ = this._teamsPlayersService.teams$;
    this.league$ = new BehaviorSubject<ILeague | undefined>(undefined);
    this.hiddenTeamsColumns = ['gender'];
    this.activeSeasonId$ = new BehaviorSubject<number | undefined>(undefined);
    this.leagueEvents$ = this._eventsService.events$;
  }

  ngOnInit(): void {
    this._route.params.subscribe((params: Params) => {
      if (params.leagueId) {
        this.leagueIdParam = params.leagueId;
        this.initComponent();
      } else {
        this._goHome();
      }
    });
  }

  initComponent(): void {
    this._subscriptions$.push(
      this._leaguesService.leagues$.subscribe(leagues => {
        this._leagues = leagues;
        this.currentLeague = this._leagues.find(league => league.id.toString() === this.leagueIdParam);
        if (this.currentLeague) {
          this._teamsPlayersService.loadTeamsByLeagueId(this.currentLeague.id);
          this.league$.next(this.currentLeague);
          this._leaguesService.selectedLeague$.next(this.currentLeague);
          this.setActiveSeason(this.currentLeague);
          this._eventsService.loadLeagueEvents(this.currentLeague.id);
        }
      }),
      this._teamsPlayersService.teams$.subscribe(teams => {
        this.leagueTeams = teams;
      })
    );
  }

  goPreviousPage(): void {
    this._goHome();
  }

  ngOnDestroy(): void {
    this._subscriptions$.forEach(sub => sub.unsubscribe());
  }

  private setActiveSeason(league: ILeague): void {
    const activeSeason = league.seasons.find(x => x.active);
    if (activeSeason) {
      this.activeSeasonId$.next(activeSeason.id);
    } else {
      this._messagingService.info('League doesnt have active season', 'League doesnt have active season');
    }
  }

  private _goHome(): void {
    this._router.navigate([APP_ROUTES.PRIVATE]);
  }
}
